  constructor(public toastr: ToastsManager, vcr: ViewContainerRef,
    private _apiServiceManager: apiServiceManager, private router: Router,
    private _mqttManager: mqttManager, private ref: ChangeDetectorRef) {

    var token = sessionStorage.getItem('user-token');
    if (!token) {
      this.router.navigate(['login']);
    }
    // enable toasters
    this.toastr.setRootViewContainerRef(vcr);
    // for internet connection check
    this.online$ = Observable.merge(
      Observable.of(navigator.onLine),
      Observable.fromEvent(window, 'online').map(() => true),
      Observable.fromEvent(window, 'offline').map(() => false)
    )

    if (sessionStorage.getItem('serviceZones')) {
      this.serviceZones = JSON.parse(sessionStorage.getItem('serviceZones'));
    }


    this.online$.subscribe((data) => {
      if (data) {
        if (!this._mqttManager._client.isConnected()) {
          console.log("-----again connected ---");
          this._mqttManager.connect();
          setTimeout(() => {
            this.getMqttMessage();
          }, 30)
        } else {
          this.getMqttMessage();
        }
      }
    })

  }

