  removeCancelledOrder(m) {
    if ((this.newOrderList.findIndex((item) => item.orderId == m.orderId)) >= 0) {
      this.toastr.warning('Order (' + m.orderId + ') from ' + m.customerDetails.name + ' has been rejected', 'Order Rejected');
      this.newOrderList.splice(this.newOrderList.findIndex((item) => item.orderId == m.orderId), 1);
      this.newCount--;
    } else if ((this.indispatchList.findIndex((item) => item.orderId == m.orderId)) >= 0) {
      this.toastr.warning('Order (' + m.orderId + ') from ' + m.customerDetails.name + ' has been rejected', 'Order Rejected');
      this.indispatchList.splice(this.indispatchList.findIndex((item) => item.orderId == m.orderId), 1);
      this.indispatchCount--;
    } else if ((this.unassignedList.findIndex((item) => item.orderId == m.orderId)) >= 0) {
      this.toastr.warning('Order (' + m.orderId + ') from ' + m.customerDetails.name + ' has been rejected', 'Order Rejected');
      this.unassignedList.splice(this.unassignedList.findIndex((item) => item.orderId == m.orderId), 1);
      this.unassignedCount--;
    } else if ((this.pickupList.findIndex((item) => item.orderId == m.orderId)) >= 0) {
      this.toastr.warning('Order (' + m.orderId + ') from ' + m.customerDetails.name + ' has been rejected', 'Order Rejected');
      this.pickupList.splice(this.pickupList.findIndex((item) => item.orderId == m.orderId), 1);
      this.pickupCount--;
    } else if ((this.assignedList.findIndex((item) => item.orderId == m.orderId)) >= 0) {
      this.toastr.warning('Order (' + m.orderId + ') from ' + m.customerDetails.name + ' has been rejected', 'Order Rejected');
      this.assignedList.splice(this.assignedList.findIndex((item) => item.orderId == m.orderId), 1);
      this.assignedCount--;
    }
    this.ref.detectChanges();
  }

