  sortOrders(result) {
    $("#appt_loader_parent").hide();
    console.log("sortOrders results ---> ", result.data);
      this.acceptedOrderList = result.data.acceptedOrder;
      this.acceptedOrderCount = result.data.acceptedOrderCount;
      this.orderPickedUpCount = result.data.pickedUpOrderCount;
      this.orderPickedUpList = result.data.pickedUpOrder;
      this.pickupReadyCount = result.data.pickUpReadyOrderCount;
      this.pickUpReadyOrderList = result.data.pickUpReadyOrder;
      this.newOrderList = result.data.newOrder;
      this.newCount = result.data.newOrderCount;
      this.indispatchList = result.data.inDispatchOrder;
      this.indispatchCount = result.data.inDispatchOrderCount;

      var date = function(d){
        return moment(d.dueDatetimeTimeStamp * 1000).format('DD, MMM YYYY');
      }

      const groupBy = (collection, iteratee = (x) => x) => {
          const it = typeof iteratee === 'function' ? 
          iteratee : ({ [iteratee]: prop }) => prop;
          const array = Array.isArray(collection) ? collection : Object.values(collection);
          return array.reduce((r, e) => {
            const k = it(e);
            r[k] = r[k] || [];
            r[k].push(e);
            return r;
          }, {});
        };

      this.acceptedOrderListData = groupBy(this.acceptedOrderList, date);
      this.listaccepted = Object.keys(this.acceptedOrderListData);
      this.newOrderListData = groupBy(this.newOrderList, date);
      this.listneworder = Object.keys(this.newOrderListData);
      this.orderPickedUpListData = groupBy(this.orderPickedUpList, date); 
      this.listOrderPickedUp = Object.keys(this.orderPickedUpListData); 
  }

