   observeDateFilter(filterDate, value) {
    this.selectedDataFrom = null;
    this.selectedDataTo = null;
    this.filterBookings = true;
    if(value == 1){
     
      this.fromDateFilter = filterDate;
    }
    else if(value == 2){
       this.toDateFilter = filterDate;
    }
   
    if (this.fromDateFilter && this.toDateFilter) {
       setTimeout(() => {
        this.selectedDataFrom = moment(this.fromDateFilter).format('YYYY-MM-DD');
        this.selectedDataTo = moment(this.toDateFilter).format('YYYY-MM-DD');
          var filterDetails = {
            cityId: this.cityId,
            index: this.indexOne,
            // index: 0,
            type: this.BookingDataType,
            franchiseId: 0,
            storeId: this.selectedStoreId,
            fromDate: this.selectedDataFrom.toString(),
            toDate: this.selectedDataTo.toString(),
            search: 0
         }
         this._apiServiceManager.getNewOrders(filterDetails).subscribe(result => {
          this.sortOrders(result);
          }, error => {
          var erRes = JSON.parse(error._body);
          switch (error.status) {
            case 498:
              this.logOutForcefully()
              break;
          }
        })
        // this.getNewBooking(true);;
       }, 10);
    }
  }

