  editOrder(selectedItem) {
    if (this.orderDetails.Items.findIndex((item) => item.addedToCartOn == selectedItem.addedToCartOn) >= 0) {
      this.editItem = selectedItem;
      this.itemQuant = selectedItem.quantity;
      this.updateItem = this.orderDetails.Items;
      $(".editOrderPopUp").fadeIn("slow");
      this.ref.detectChanges();
    };
  }

  cancelUpdate() {
    this.editItem = [];
    this.updateItem = [];
    this.itemQuant = 1;
    $(".editOrderPopUp").fadeOut();
  }

  updateOrder(type) {
    let initialQuant = this.itemQuant;
    let iD = this.orderDetails.Items.findIndex((item) => item.addedToCartOn == this.editItem.addedToCartOn);
    let selectedItem = this.updateItem[iD];
    switch (type) {
      case 1:
        if (iD >= 0) {
          this.itemQuant > 0 ? this.updateItem[iD].quantity = this.itemQuant : this.updateItem.splice(iD, 1);
        }
        break;
      case 2:
        this.updateItem.splice(iD, 1);
        break;
    }
    
    var orderDet = {
      items: this.updateItem,
      ipAddress: sessionStorage.getItem("ip"),
      latitude: sessionStorage.getItem("lat"),
      longitude: sessionStorage.getItem("long"),
      extraNote: this.orderDetails.extraNote,
      deliveryCharge: this.orderDetails.deliveryCharge,
      orderId: this.orderDetails.orderId
    }

    if (this.orderDetails.Items.length == 0) {
      $('.editOrderPopUp').hide();
      swal({
        title: 'Cancel Order',
        text: 'Are you sure you want to cancel the Order:' + this.orderDetails.orderId,
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
        cancelButtonText: 'No',
        confirmButtonClass: 'btn btn-warning',
        cancelButtonClass: 'marginLeft btn btn-success ',
        buttonsStyling: false,
        reverseButtons: false
      }).then((result) => {
        if (result.value) {
          this.deleteOrder(this.orderDetails.orderId);
        } else {
          this.orderDetails.Items.unshift(selectedItem)
          this.itemQuant = initialQuant;
          this.ref.detectChanges();
        }
      })
    } else {
      this._apiServiceManager.editOrder(orderDet).subscribe(result => {
        this.orderDetails = result.data;
        this.itemList = result.data.Items;
          this.getNewBooking(false);
        this.ref.detectChanges();
        this.cancelUpdate();
      }, error => {
        var erRes = JSON.parse(error._body);
        switch (error.status) {
          case 498:
            this.logOutForcefully()
            break;
          case 504:
            swal({
                title: 'Sorry !',
                text: "Internal Server Error : 504",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                buttonsStyling: false,
                reverseButtons: false
              })
            break;
        }
      })
    }
  }

  readyToDelete(item) {
    let id = item.orderId;
    swal({
      title: 'Are you sure ?',
      text: ' Order:' + id + ' will be cancelled.',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes',
      cancelButtonText: 'No',
      confirmButtonClass: 'btn btn-warning',
      cancelButtonClass: 'marginLeft btn btn-success ',
      buttonsStyling: false,
      reverseButtons: false
    }).then((result) => {
      if (result.value) {
        this.deleteOrder(id);
      }
    })
  }

  deleteOrder(id) {
    let deleteOrderDetails = {
      ipAddress: sessionStorage.getItem("ip"),
      orderId: id,
      latitude: parseFloat(sessionStorage.getItem("lat")),
      longitude: parseFloat(sessionStorage.getItem("long")),
      reason:""
    }
    this._apiServiceManager.deleteOrder(deleteOrderDetails).subscribe(result => {
      this.editItem = [];
      this.updateItem = [];
      this.itemQuant = 1;
      $(".editOrderPopUp").fadeOut();
      $('#myMod').modal('hide');
      let oId = parseInt(result.data.orderId);
      // let orderId = "edcdscd";
      this.removeCancelledOrder(result.data);
    }, error => {
      let errorMessage = JSON.parse(error._body);
      switch (error.status) {
          case 498:
            this.logOutForcefully();
            break;
          case 504:
            swal({
                title: 'Sorry !',
                text: "Internal Server Error : 504",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                buttonsStyling: false,
                reverseButtons: false
              })
            break;
          case 400:
            swal({
                title: 'Sorry !',
                text: "Internal Server Error : 400",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                buttonsStyling: false,
                reverseButtons: false
              })
            break;
        }
      // swal(
      //   'Sorry!',
      //   errorMessage.message,
      //   'error'
      // )
    })
  }

