import { Component, OnInit } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { apiServiceManager } from '../service/api-service-manager';

declare var jQuery: any;
declare var $: any;
declare var Fingerprint: any;
import * as moment from 'moment';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [apiServiceManager],
})


export class LoginComponent implements OnInit {
  userEmail: any;
  password: any;
  forgetpassresult:any;
  mailResponse_err: any;
  password_err: any;
  login_err: any;
  rememberMeChecked:boolean = false;
  deviceId: any;
  usesrType:any =1;
  nowDateTimestamp:any;
  savedEmail:any;
  savedPassword:any;
  constructor(private _apiServiceManager: apiServiceManager, private router: Router) { }


  ngOnInit() {
      if(localStorage.getItem('savedEmail')) {
        this.rememberMeChecked  = true;
        this.userEmail = localStorage.getItem('savedEmail');
        this.password = localStorage.getItem('savedPassword');
      } else {
        this.rememberMeChecked  = false;
        this.userEmail = '';
        this.password = '';
        localStorage.clear();
      }

      if (sessionStorage.getItem('user-token')) {
          this.router.navigate(['dispatcher']);
      }
        this.deviceId = new Fingerprint().get();
        this.nowDateTimestamp = + new Date();
        $("#hidepass").show();
        $("#submitPass").hide();
    
  }

  rememberMeCheck(value){
    console.log("value -->", value);
    if(value.checked == true){
      this.savedEmail = localStorage.setItem('savedEmail', this.userEmail);
      this.savedPassword = localStorage.setItem('savedPassword', this.password);
      // this.userEmail = localStorage.getItem('savedEmail');
      // this.password = localStorage.getItem('savedPassword');
    } else {
      this.savedEmail = localStorage.removeItem('savedEmail');
      this.savedPassword = localStorage.removeItem('savedPassword');
      this.userEmail = this.savedEmail;
      this.password = this.savedPassword;
      localStorage.clear();
    }
    
}

  ngOnDestroy() {
    $("app-login").hide();
  }
  validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  }

  checkEmail() {
    var mailResponse = this.validateEmail(this.userEmail.trim());
    if (!mailResponse) {
      this.mailResponse_err = "Enter a valid Mail ID"
    };
  }

  clearAll() {
    this.mailResponse_err = '';
    this.password_err = '';
    this.login_err = '';
  }

 loginKeyPressHandler(e){
    e.keyCode == 13 ? this.dispatcherLogin() : '';
  }

  dispatcherLogin() {
    $('.form-control').addClass("loadinggif");
    var mailResponse = this.validateEmail(this.userEmail.trim());
    if (!mailResponse) {
      $('.form-control').removeClass("loadinggif");
      this.mailResponse_err = "  Enter a valid Mail ID"
    } else if (!this.password) {
      $('.form-control').removeClass("loadinggif");
      this.password_err = " Password is Required"
    }

    if (this.password && mailResponse) {

      var data = {
        email: this.userEmail.trim(),
        password: this.password,
        deviceId:this.deviceId+""+this.nowDateTimestamp    
      }

      this._apiServiceManager.login(data).subscribe(result => {
        $('.form-control').removeClass("loadinggif");
        sessionStorage.setItem('storeId', result.data.storeId);
        sessionStorage.setItem('franchiseId' , result.data.franchiseId);
        sessionStorage.setItem('managerId', result.data.id);
        sessionStorage.setItem('isForcedAccept', result.data.isForcedAccept);
        sessionStorage.setItem('isAutoDispatch', result.data.isAutoDispatch);
        sessionStorage.setItem('storeName', result.data.storeName);
        sessionStorage.setItem('franchiseName', result.data.franchiseName);
        sessionStorage.setItem('cityName', result.data.cityName);
        sessionStorage.setItem('cityId' , result.data.cityId);
        sessionStorage.setItem('user-token', result.data.authToken);
        sessionStorage.setItem('userType' , result.data.userType)
        sessionStorage.setItem('driverType' , result.data.driverType);
        sessionStorage.setItem('bufferTime' , result.data.bufferTime);
        sessionStorage.setItem('currencySymbol' , result.data.currencySymbol);
        sessionStorage.setItem('scheduledBookingEnable' , result.data.scheduledBookingEnable);

        result.data.serviceZones.length > 0 ?
        sessionStorage.setItem('serviceZones', JSON.stringify(result.data.serviceZones))
        : sessionStorage.setItem('serviceZones', '')

        // driverType
        this.router.navigate(['dispatcher']);
      }, error => {
        this.login_err = JSON.parse(error._body);
        console.log("message error", this.login_err.message);
        $('.form-control').removeClass("loadinggif");
      }, () => {
        console.log("completed");
      })
    }
  }

  usesrTypeChange(type){
    this.usesrType = type;
  }

  hidepass(){
    $("#hidepass").hide();
    $("#submitPass").show();
    $("#forgotpasswordLink").hide();
    $(".checkbox").hide();
     $("#ImageButton1").hide(); 
  }

  forgetpassword(){
     var data = {
        emailOrMobile:this.userEmail,
        verifyType: 2
        // emailOrMobile: this.userForgetPasswordEmail,
    }
    this._apiServiceManager.forgetpassword(data).subscribe(result => {
      this.forgetpassresult = result.data.message;
      $("#submitPass").hide();
      $("#hidepass").show();
      $("#ImageButton1").show(); 
      console.log("forget password",result);
    }, error => {
      var erRes = JSON.parse(error._body);
    })
  }
}


