import { Component, OnInit, NgModule, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource } from '@angular/material';
import { RouterModule, Routes } from '@angular/router';
import { ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { Paho } from 'ng2-mqtt';
import { apiServiceManager } from '../../service/api-service-manager';
import { mqttManager } from '../../service/mqtt-manager.service';
import { DatePipe } from '@angular/common';
import { DecimalPipe } from '@angular/common';
import swal from 'sweetalert2';

declare var $: any;
declare var jQuery: any;
declare var Fingerprint: any;
declare var google: any;
declare var placeMarkerAndPanTo: any;
declare var value: any;
declare var flatpickr: any;
declare var flatpickr2: any;

@Component({
  selector: 'app-tasks',
  templateUrl: './tasks.component.html',
  styleUrls: ['./tasks.component.css', '../dispatcher-home/dispatcher-home.css']
})
export class TasksComponent implements OnInit {
  orderDetails: any;
  itemList: any;
  pastOrderList: any = [];
  forcedAcceptValue: any = sessionStorage.getItem('isForcedAccept');
  autoDispatcheValue:any = sessionStorage.getItem('isAutoDispatch');
  driverType:any = sessionStorage.getItem('driverType');
  userType : any  = sessionStorage.getItem('userType');
  createdDate: any;
  acceptedDate: any;
  onTheWay: any;
  arrivedAt: any;
  jobStarted: any;
  completed: any;
  dataSource: any;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  constructor(private _apiServiceManager: apiServiceManager, private router: Router, private ref: ChangeDetectorRef) { }

  displayedColumns = ['Order-Id', 'Name','OrderCategory', 'Mobile', 'Email', 'Driver', 'OrderTime', 'DeliveryTime', 'deliveryCharge', 'Cart-Total', 'Status', 'Drop-Location'];
  

  ngOnInit() {
    jQuery('#back_button').show();
    jQuery('#godsView').show();
    this.getPastJobs();
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }



  /**
   * Set the paginator after the view init since this component will
   * be able to query its view for the initialized paginator.
   */


  getPastJobs() {
    var PastOrders = {
      sid: sessionStorage.getItem('storeId') || 0,
      index: 0
    }
    $(".rotate").attr("id", "loading");
    this._apiServiceManager.getPastJobs(PastOrders).subscribe(result => {
      $(".rotate").attr("id", "");
      this.pastOrderList = result.data.pastOrders;
      this.dataSource = new MatTableDataSource(this.pastOrderList);
      this.dataSource.paginator = this.paginator;
      this.ref.detectChanges();
    }, error => {
      var erRes = JSON.parse(error._body);
      switch (error.status) {
        case 498:
          this.logOutForcefully();
          break;
      }
    });
  }

  autoFillOrder(val) {
    this.pastOrderList = [];

    if (val.length == 0) {
      $(".rotate").attr("id", "");
      // $(".refresh").removeClass("loadinggif");
      this.getPastJobs();
      this.ref.detectChanges();
    } else {
      this.searchPastOrders(val);
      this.ref.detectChanges();
    }



  }

  searchPastOrders(needle) {
    let PastOrder = {
      sid: sessionStorage.getItem('storeId'),
      index: 0,
      search: needle
    }
    $(".rotate").attr("id", "loading");
    // $(".refresh").addClass("loadinggif");
    this._apiServiceManager.searchOrder(PastOrder).subscribe(result => {
      $(".rotate").attr("id", "");
      // $(".refresh").removeClass("loadinggif");   
      this.pastOrderList = result.data.pastOrders;
      this.dataSource = new MatTableDataSource(this.pastOrderList);
      this.dataSource.paginator = this.paginator;

      this.ref.detectChanges();
    }, error => {
      var erRes = JSON.parse(error._body);
      switch (error.status) {
        case 498:
          this.logOutForcefully();
          break;
      }
    });
  }
  logOutForcefully() {
    swal({
      title: 'Sorry !',
      text: "Your session has expired, please login again to access the dispatcher.",
      type: 'warning',
      showCancelButton: false,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Login',
      confirmButtonClass: 'btn btn-success',
      buttonsStyling: false,
      reverseButtons: false
    }).then((result) => {
      $('.modal').modal('hide');
      this.router.navigate(['login']);
      sessionStorage.removeItem("loginUser");
      sessionStorage.removeItem("user-token");
    });
  }
  show_details(item) {
    // $(".decideJob").hide('slow');
    // this.orderDetails = "";
    // this.itemList = "";
    this.orderDetails = item;
    this.itemList = item.Items;
    this.ref.detectChanges();
    $('#myMod').modal('show');
    // if (this.orderDetails.timeStamp.created) {
    //   this.completed = this.orderDetails.timeStamp.completed.timeStamp;
    // }
    // if (this.orderDetails.timeStamp.journeyStart) {
    //   this.jobStarted = this.orderDetails.timeStamp.journeyStart.timeStamp;
    // }
    // if (this.orderDetails.timeStamp.arrived) {
    //   this.arrivedAt = this.orderDetails.timeStamp.arrived.timeStamp;
    // }
    // if (this.orderDetails.timeStamp.onTheWay) {
    //   this.onTheWay = this.orderDetails.timeStamp.onTheWay.timeStamp;
    // }
    // if (this.orderDetails.timeStamp.accepted) {
    //   this.acceptedDate = this.orderDetails.timeStamp.accepted.timeStamp;
    // }
    // if (this.orderDetails.timeStamp.created) {
    //   this.createdDate = this.orderDetails.timeStamp.created.timeStamp;
    // }
  }
}





