  constructor(public toastr: ToastsManager, vcr: ViewContainerRef, private _apiServiceManager: apiServiceManager, private router: Router, private _mqttManager: mqttManager, private ref: ChangeDetectorRef) {

    this.toastr.setRootViewContainerRef(vcr);

    this.online$ = Observable.merge(
      Observable.of(navigator.onLine),
      Observable.fromEvent(window, 'online').map(() => true),
      Observable.fromEvent(window, 'offline').map(() => false)
    )

    this.online$.subscribe((data) => {
      if (data) {
        console.log("isConnected", this._mqttManager._client.isConnected());
        if (!this._mqttManager._client.isConnected()) {
          this._mqttManager.connect();
          setTimeout(() => {
            this.getMqttMessage();
          }, 30)
        } else {
          this.getMqttMessage();
        }
      }
    })
  }

  curlat: any;
  curlong: any;
  map: any;
  initMapbox = async () => {
    // let marker;
    if (navigator.geolocation) {
      await navigator.geolocation.getCurrentPosition(async (e) => {
        var position_marker = new google.maps.LatLng(e.coords.latitude, e.coords.longitude);
        this.curlat = await e.coords.latitude;
        this.curlong = await e.coords.longitude;
        this.setMapBox(e.coords.latitude, e.coords.longitude)
        await console.log("longitudelongitudelongitudelongitude", e.coords.latitude, e.coords.longitude);
      });
    } else {
    }

  }

  plotMarkers(drivers) {
    if (drivers.length > 0) {
      for (let inst = 0; inst < drivers.length; inst++) {
        drivers[inst].lastOnline ? drivers[inst].lastUpdatedMinutes = moment.utc(drivers[inst].lastOnline * 1000).fromNow() : ''
        let icn;
        let allmarker;
        let marker;

        switch (drivers[inst].status) {
          case 3:
            icn = 'assets/images/active.png';
            break;
          case 4:
            icn = 'assets/images/offline.png';
            break;
          case 9:
            icn = 'assets/images/inactive.png';
            break;
          case 8:
            icn = 'assets/images/loggedout.png';
            break;
        }

        marker = L.marker([drivers[inst].latitude, drivers[inst].longitude]);
        var content = '<div style="width:100px;text-align:center"> <p><strong>' + drivers[inst].name + '</strong></p><img src="" alt=""> </div>';
        marker.bindPopup(content);
        marker.id = drivers[inst]._id;
        marker.status = drivers[inst].status;
        this.SelectedMarkersArray.push(marker);
        this.clusterGroup.addLayer(marker);
        this.map.addLayer(this.clusterGroup);
        allmarker = L.marker([drivers[inst].latitude, drivers[inst].longitude]);
        allmarker.id = drivers[inst].id;
        allmarker.status = drivers[inst].status;
        marker = L.marker([drivers[inst].latitude, drivers[inst].longitude]);
        // this.all_markers.push(allmarker);
        // this.all_drivers_index.push(drivers[inst].id);
        // this.changeLastseen();
        // this.search_done = false;
      }
    }
  }


