  getMqttMessage() {
    this._mqttManager.getMessages().subscribe((message: Paho.MQTT.Message) => {
      console.log("-----------------message-------------", message);
      console.log("getMqttMessage destination name", message.destinationName);

      let mqttMessage = JSON.parse(message.payloadString)

      switch (mqttMessage.destinationName) {
        case 'driverStatus':
          this.providerOne = JSON.parse(message.payloadString);
          console.log("provider", this.providerOne);
          this.providerChanges(this.providerOne);
          this.ref.detectChanges();
          break;

        case 'driverLocation':
          this.providerTrack = JSON.parse(message.payloadString);
          console.log("this.providerTrack", this.providerTrack);
          this.driverLocationUpdate(this.providerTrack);
          // this.providerLocationUpdate(this.providerTrack);
          this.ref.detectChanges();
          break;

        case 'orderUpdate':
          // this.trackBooking(JSON.parse(message.payloadString));
          this.ref.detectChanges();
          break;
      }
    });
  }

  map_load() {
        const input = document.getElementById('pac-input');
        const autocomplete = new google.maps.places.Autocomplete(input);
        autocomplete.addListener('place_changed', () => {
            // this.clearSearchLocationDetails();
            var place = autocomplete.getPlace();
            const lat = place.geometry.location.lat();
            const long = place.geometry.location.lng();
            if (this.clusterGroup) {
                this.map.removeLayer(this.clusterGroup);
            }
            if (place.geometry.location.lat()) {
              this.clusterGroup = L.marker([lat, long]).addTo(this.map);
                this.map.setView([lat, long], 16)
            }
        })
    }


