import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Paho } from 'ng2-mqtt';
import 'rxjs/Rx';
declare var jQuery: any;
import * as moment from 'moment';

@Injectable()
export class mqttManager {

  public messageData: any = [];

  public _client: Paho.MQTT.Client;

  constructor(private router: Router) {
    var time = moment().format();
    var id = sessionStorage.getItem('managerId') + moment().unix();
   // this._client = new Paho.MQTT.Client("wss://mqttdelivx.instacart-clone.com:2083/mqtt", id);
     this._client = new Paho.MQTT.Client("wss://websocket.trolley.app:2083/mqtt", id);
    // this._client = new Paho.MQTT.Client("wss://mqtt-ws-staging.loopzdelivery.com:2083/mqtt", id);
    // this._client = new Paho.MQTT.Client("ws://mqtt://13.57.51.155", 2052 , "/mqtt", id);
  }
  
  connect() {
    this._client.connect({ 
      onSuccess: this.onConnected.bind(this),
      userName : "mandobiVernemq",
      password : "5anmTNqxcac659sZ",
      timeout: 15,
      keepAliveInterval: 15,
      cleanSession: false,
     });
  }

  connectionLost() {
    let observable = new Observable(observer => {
      this._client.onConnectionLost = (responseObject: Object) => {
        console.log("onConnectionLost", responseObject);
        observer.next(responseObject);
      };
    })
    return observable;
  }

  getMessages() {
    let observable = new Observable(observer => {
      this._client.onMessageArrived = (message: any) => {
        observer.next(message);
      };
    })
    return observable;
  }

  connectToDriver(zoneId){
    console.log('connect', zoneId);
    this._client.subscribe(zoneId, { qos: 2 });
  }

  connectToLiveDriver(data){
    this._client.subscribe(data, { qos: 2 });
  }

  disconnectToLiveDriver(driver){
    this._client.unsubscribe(driver, { qos: 2 });
  }

  private onConnected(): void {
    console.log("mqtt connected"); 
    this._client.subscribe("dispatcher/freelancer/"+ sessionStorage.getItem('cityId') , { qos: 2 });

    if (sessionStorage.getItem('userType') == '0') {//city
      console.log('Connected to  city.');
      this._client.subscribe("cityManager/" + sessionStorage.getItem('cityId') + "/" + sessionStorage.getItem('managerId'), { qos: 2 });
    } else if (sessionStorage.getItem('userType') == '1') {//franchies
      console.log('Connected to  franchise.');
      this._client.subscribe("dispatcher/" + sessionStorage.getItem('cityId') + "/" + sessionStorage.getItem('franchiseId') + "/#", { qos: 2 });
    } else if (sessionStorage.getItem('userType') == '2') {//store
      console.log('Connected to  store.');
      this._client.subscribe("storeManager/" + sessionStorage.getItem('cityId') + "/" + sessionStorage.getItem('franchiseId') + "/" + sessionStorage.getItem('storeId')+"/"+sessionStorage.getItem('managerId') , { qos: 2 });
    }
    console.log('Connected to broker.');
  }
}





