addonFinalSubmit(){
      let len = this.radioAddonArray.length > this.checkboxAddonArray.length ? this.radioAddonArray.length : this.checkboxAddonArray.length;
      for (var k = 0; k < len; k++){
        if( this.radioAddonArray.length > k )this.innerOuterArray.push(this.radioAddonArray[k]);
        if( this.checkboxAddonArray.length > k )this.innerOuterArray.push(this.checkboxAddonArray[k]);
      }
     
      for(var i=0; i<this.addonArray.length; i++){
        let indexI = this.innerOuterArray.findIndex((itm) => itm.id == this.addonArray[i].id);
       if(this.innerOuterArray[indexI].id == this.addonArray[i].id){
          for(var j=0; j<this.innerOuterArray.length; j++){
              if(this.innerOuterArray[j].id){
                this.addonInnerArray.push(this.innerOuterArray[j].addOnGroup);
                this.newAddonArray.push(
                      {
                        "addOnGroup":this.addonInnerArray,
                        "id":this.innerOuterArray[indexI].id
                        // "id":this.addonArray[i].id
                      });
            } else {
              this.addonInnerArray = [];
            
            }
            
          }
        } 
       
       
        // for selecting only mandatory addons not other addons
        if(this.innerOuterArray.length == this.newAddonArray.length){
          break;
        }
      }

      this.newAddonArray = this.newAddonArray.filter((ele,index) => {
        return index === this.newAddonArray.findIndex(obj => {
          return JSON.stringify(obj.id) === JSON.stringify(ele.id);
        });
      });
     
      
}

  removeCart() {
    swal({
      title: 'Are you sure?',
      text: "This cart will be cleared!",
      type: 'warning',
      confirmButtonColor: '#3085d6',
      confirmButtonText: 'Clear',
      confirmButtonClass: 'width-140 mar-rgt-10 btn btn-success',
      buttonsStyling: false,
      reverseButtons: true
    }).then((result) => {
        if(result.value == true){
          this.clearCart();
        } else if(result.dismiss == 'overlay'){
          console.log("discart cart orders");
        } 
    })
    // cancelButtonText: 'Cancel',
    //cancelButtonColor: '#d33',
    //cancelButtonClass: 'width-140 btn btn-danger',
    // showCancelButton: true,
  }

  syncCustomList(cartData) {
    if (cartData.orderType == 2) {
      for (let i = 0; i < cartData.products.length; i++) {
        let childProductIdMatch = this.customItemList.findIndex((item) => item.childProductId == cartData.products[i].childProductId);
        if (childProductIdMatch >= 0) {
          this.customItemList[childProductIdMatch].quantity = cartData.products[i].quantity;
          this.customItemList[childProductIdMatch].finalPrice = cartData.products[i].finalPrice;
          this.ref.detectChanges();
        }
      }
    }
  }

  syncProducList(cartData) {
    if (cartData.orderType == 1) {
      for (let i = 0; i < cartData.products.length; i++) {
        let childProductIdMatch = this.productList.findIndex((item) => item.childProductId == cartData.products[i].childProductId);
        let parentProductIdMatch = this.productList.findIndex((item) => item.parentProductId == cartData.products[i].parentProductId);

        if (childProductIdMatch >= 0 && parentProductIdMatch >= 0 && (childProductIdMatch == parentProductIdMatch)) {
          let unitIndex = this.productList[childProductIdMatch].units.findIndex((item) => item.unitId == cartData.products[i].unitId);
          if (unitIndex >= 0) {
            this.productList[childProductIdMatch].units[unitIndex].quantity = cartData.products[i].quantity;
            this.productList[childProductIdMatch].units[unitIndex].finalPrice = cartData.products[i].finalPrice;
          }
          this.ref.detectChanges();
        }
      }
    }
  }

  clearCart() {
    this.uiForDeliveryService = false;
    this.selectedCategory = "";
    this.selectedSubCatId = ""; 
    this.selectedSubSubCatId = "";
    this.extranote = '';
    this.estimatedPackageValue = '';
    this.JobPaymentType.reset();
    this.deliveryCharge = null;
    this.paymentTypeValue = '';
    if (this.cartArray && this.cartArray.cartId) {
      let cartData = {
        customerId: this.customerId,
        cartId: this.cartArray.cartId
      }
      this._apiServiceManager.clearCart(cartData).subscribe(result => {
        this.cartOrderType == 3 ? this.bulkOrderList = [] : "";
        this.cartOrderType == 1 ? this.productList = [] : "";
        this.cartOrderType == 2 ? this.customItemList = [] : "";
        this.checkOutList = [];
        this.checkOutCartList = [];
        this.cartArray = null;
        this.myCartList = false;
        this.cartOrderType = null;
        this.ref.detectChanges();
        this.getCart();
      }, error => {
        switch (error.status) {
          case 498:
            this.logOutForcefully();
            break;
          case 504:
            swal({
                title: 'Sorry !',
                text: "Internal Server Error : 504",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                buttonsStyling: false,
                reverseButtons: false
              })
            break;
        }
      })
    }
  }




  checkPromoCode(promocode) {
    let params = {
      "userId": this.customerId,
      "couponCode": promocode,
      "cityId": sessionStorage.getItem('cityId'),
      "zoneId": "",
      "paymentMethod": 0,
      "vehicleType": 0,
      "deliveryFee": this.deliveryCharge,
      "cartValue": this.cartArray.finalTotalIncludingTaxes,
      "finalPayableAmount": this.cartArray.finalTotalIncludingTaxes
    }
    this._apiServiceManager.checkPromoCode(params).subscribe(result => {
      this.promoDiscountAmount = result.data.discountAmount;
      result.data.discountAmount ? this.cartArray.cartDiscount += result.data.discountAmount : "";
      result.data.discountAmount ? this.cartArray.finalTotalIncludingTaxes -= result.data.discountAmount : "";
      this.cartArray.finalTotalIncludingTaxes < 0 ? this.cartArray.finalTotalIncludingTaxes = 0 : "";
      this.promoId = result.data.promoId;
      this.ref.detectChanges();
    }, error => {
        switch (error.status) {
          case 498:
            this.logOutForcefully();
            break;
          case 504:
            swal({
                title: 'Sorry !',
                text: "Internal Server Error : 504",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                buttonsStyling: false,
                reverseButtons: false
              })
            break;
        }
    })
  }

  updateProductArray() {
    let index = this.productList.findIndex((item) => item.childProductId == this.selectedProduct.childProductId);
    this.productList[index].inCart = true;
    let unitIndex = this.productList[index].units.findIndex((item) => item.unitId == this.selectedUnit.unitId);
    this.productList[index].units[unitIndex].inCart = true;
  }

  
