  GetDeliveryFee() {
    if (!(this.JobPaymentType.hasError('required'))){

      let fareData = {
      customerId: this.customerId,
      status: 1,
      type: 2,
      latitude: this.pickuplat,
      longitude: this.pickuplong,
      pickUpLat:this.pickuplats || "",
      pickUpLong:this.pickuplongs || ""
    }

    this._apiServiceManager.getFare(fareData).subscribe(result => {
      this.deliveryCharge = result.data[0].storeDeliveryFee;
      this.uiForDeliveryService = true;
      result.data[0].storeDeliveryFee ? this.cartArray.finalTotalIncludingTaxes += this.deliveryCharge : "";
    }, error => {
      
        switch (error.status) {
          case 400:
            this.uiForDeliveryService = false;
            swal({
                title: 'Sorry !',
                text: "we do not deliver to your selected address at this moment as it’s out of delivery reach.",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                // buttonsStyling: false,
                // reverseButtons: false
              })
            break;
          case 498:
            this.logOutForcefully();
            break;
          case 504:
            swal({
                title: 'Sorry !',
                text: "Internal Server Error : 504",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonClass: 'btn btn-success',
                buttonsStyling: false,
                reverseButtons: false
              })
            break;
        }
    })
    this.ref.detectChanges();
    } else {
          swal({
            title: 'Please select Payment Mode',
            text: "After that click on Get Delivery Fee Button",
            type: 'warning',
            showCancelButton: false,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonClass: 'btn btn-success',
            buttonsStyling: false,
            reverseButtons: false
          })
    }
  }
  
  checkOutCart() {
    // checkOutCart
    let nowDate = moment().format('YYYY-MM-DD HH:mm:ss');
    let date = $(".myDate").val() + " " + $(".book_later_time").val() + ":00";
    this.bookingType ? this.bookingType = this.bookingType : this.bookingType = 1;
    this.serviceType ? this.serviceType = this.serviceType : this.serviceType = 1;

    let storeIdSpecial ={};
    if(this.userType == 2) {
      storeIdSpecial[sessionStorage.getItem('storeId')]=this.extranote;
    } else if(this.storeTypeVal == 7){
      storeIdSpecial["1"]=this.extranote; 
    }
    else if(typeof storeIdSpecial[this.selectedStore.storeId]=="undefined"){
        storeIdSpecial[this.selectedStore.storeId]=this.extranote; 
    }
    let cartData = {
      customerId: this.customerId,
      serviceType: this.serviceType,
      bookingType: this.bookingType,
      cartId: this.cartArray.cartId,
      bookingDate: nowDate,
      extraNote:storeIdSpecial,
      address1:this.addLine1 || "",
      address2:this.addLine2 || "",
      addressId:this.addressId || "",
      deviceTime:nowDate,
      pickUpLat :this.pickuplats || "",
      pickUpLong:this.pickuplongs || "",
      estimatedPackageValue:parseFloat(this.estimatedPackageValue) || "",
      storeType:this.storeTypeVal || "",
      storeTypeMsg:this.categoryNames || ""
    };
    // selectedCard
    this.promocode ? cartData['couponCode'] = this.promocode : "";
    this.promoDiscountAmount > 0 ? cartData['discount'] = this.promoDiscountAmount : cartData['discount'] = 0;
    this.promoDiscountAmount > 0 && this.promoDiscountAmount == 2 ? cartData['cardId'] = this.selectedCard : "";
    this.paymentTypeValue ? cartData['paymentType'] = this.paymentTypeValue : cartData['paymentType'] = 1;
    this.serviceType == 2 ? cartData['latitude'] = 0 : cartData['latitude'] = this.pickuplat;
    this.serviceType == 2 ? cartData['longitude'] = 0 : cartData['longitude'] = this.pickuplong;
    this.bookingType == 2 ? cartData['dueDatetime'] = date : "";
    this.bookingType == 1 ? cartData['dueDatetime'] = nowDate : "";

    this._apiServiceManager.checkOutCart(cartData).subscribe(result => {
      $('#myModal').modal('hide');
      this.refreshData();
    }, error => {
      swal(
        'Sorry!',
        "Something Went wrong, couldn't checkout your cart",
        'error'
      );
    })
  }

  schedule(val) {
    if (val == 1) {
      this.selectedSchedule = true;
      this.selected = true;
      this.bookingType = 1;
      $(".bookButton").removeClass("activeSchedule")
      $(".bookNow").addClass("activeSchedule");
    } else if (val == 2) {
      this.selectedSchedule = false;
      this.selected = true;
      this.bookingType = 2;
      setTimeout(() => {
        this.set_flatpickr();
      }, 50)

      $(".bookButton").removeClass("activeSchedule")
      $(".bookLater").addClass("activeSchedule")
    }

  }

  set_flatpickr() {
    let buffer = this.bufferTime;
    let current_time: any = moment().add(0, 'hours').startOf('minutes');
    console.log("current_time ====>", current_time);
    current_time = current_time.add(buffer, 'minutes').format('hh:mm A');
    console.log("current_time after ====>", current_time);

    flatpickr(".flatpickr", {
      defaultDate: moment().format('YYYY-MM-DD '), // Date objects and date strings are also accepted
      enableTime: false,
      enableSeconds: false,
      minDate: moment().format('YYYY-MM-DD'), // "today" / "2016-12-20" / 1477673788975
      maxDate: "2019-12-20",
      altFormat: "M j, Y"
    });
    
    flatpickr(".timepicker", {
      defaultDate: current_time, // Date objects and date strings are also accepted
      enableTime: true,
      allowInput: true,
      enableSeconds: false,
      altFormat: "H:i",
      noCalendar: true,
      minuteIncrement: 30,
      time_24hr: true, // AM/PM time picker is used by default
      // default format
      dateFormat: "H:i",

      // initial values for time. don't use these to preload a date
      defaultHour: moment().format('hh'),
      defaultMinute: moment().format('mm'),
    });
  }

