  getRecentAddress() {
    this._apiServiceManager.getCustomerAddress(this.customerId).subscribe(result => {
      this.savedCards = result.data.savedCards;
      this.savedCards ? this.CardSelectDisable = false : "";
      let condition1 = false;
      let condition2 = false;
      if (result.data.defaultAddress.addLine1) {
        condition1 = true;
        // if recent address exist add it to address input
        let address = result.data.defaultAddress;
        this.customer_Location = address.addLine1 + "," + address.addLine2 + "," + address.city + "," + address.state + "," + address.country + "-" + address.pincode;
        this.customerPickup_Location = address.addLine1 + "," + address.addLine2 + "," + address.city + "," + address.state + "," + address.country + "-" + address.pincode;
        this.addressId = result.data.defaultAddress._id;
        // select the array 
        this.recentAddress = result.data.defaultAddress;
        this.pickuplat = result.data.defaultAddress.latitude;
        this.pickuplong = result.data.defaultAddress.longitude;
        console.log("getRecentAddress this.pickuplat", this.pickuplat);
        console.log("getRecentAddress this.pickuplong", this.pickuplong);
        // this.getCategory();
      };
      if (result.data.savedAddresses.length > 0) {
        condition2 = true;
        // old addresses selected by customer 
        this.previousLocationList = result.data.savedAddresses;
      }
      if (!condition2 && !condition1) {
        this.setForNewAddress();
      }
    })
  }
