  addAddress() {
    if( !(this.addrTag.hasError('required')) || !(this.addrTagPick.hasError('required'))) {
      console.log("this.pickuplat", this.pickuplat);
      let address = {
        addLine1: this.addLine1 || this.takeDelivAddressLine1 || this.takePickAddressLine1,
        addLine2: this.addLine2 || this.takeDelivAddressLine2 || this.takePickAddressLine2,
        latitude: this.pickuplat || this.pickuplats,
        longitude: this.pickuplong || this.pickuplongs,
        taggedAs: this.addressTag || this.pickupAddressTag,
        landmark: this.customer_landmark || this.customerPickup_landmark,
        customerId: this.customerId,
        city: this.addLocationCity || this.cityMap || this.cityMapPick,
        state: this.addLocationState || this.stateMap || this.stateMapPick,
        country: this.addLocationCountry || this.countryMap || this.countryMapPick,
        pincode: this.addpincode || this.pincode || this.pincodePick,
      }
      this._apiServiceManager.addCustomerAddress(address).subscribe(result => {
        if (result.data) {
          this.addressId = result.data._id;
        }
        this.getRecentAddress();
        this.resetForAddress();
        this.resetForPickupAddress();
        if(this.userType == 2){
          this.categorySelect(this.selectedStore.storeId);
          // this.getCategory();
        }
        
      }, error => {
          switch (error.status) {
            case 498:
              this.logOutForcefully();
              break;
            case 504:
              swal({
                  title: 'Sorry !',
                  text: "Internal Server Error : 504",
                  type: 'warning',
                  showCancelButton: true,
                  confirmButtonColor: '#3085d6',
                  cancelButtonColor: '#d33',
                  confirmButtonClass: 'btn btn-success',
                  buttonsStyling: false,
                  reverseButtons: false
                })
              break;
          }
        })
    } else {
        swal({
          title: 'Select Address tag',
          text: "After that you can save it!!",
          type: 'warning',
          showCancelButton: false,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonClass: 'btn btn-success',
          buttonsStyling: false,
          reverseButtons: false
        })
    };
   
  }

